<template>
	<view class="contain">
        <image class="logo" src="/static/logo.jpg"></image>
        <p class="remark">省钱就来一起拼</p>
		<u-button type="success" @click="loginClick" class="custom-style" ripple-bg-color="#909399">
			<u-icon name='weixin-fill' color="#ffffff" size="40" class="loginIcon"></u-icon>
			微信快捷登入
		</u-button>
		<p class="des">微信授权登入后可以享受拼单,低价购物服务</p>


	</view>

</template>


<script>
	export default {
		data() {
			return {
			}
		},
		methods: {

			loginClick() {

wx.getSetting({
  success(res) {
    if (res.authSetting['scope.userInfo']) {
      wx.authorize({
        scope: 'scope.userInfo',
        success () {
          wx.getUserInfo({
            success: function(res) 
            {
                uni.setStorageSync('UserInfo', res);
                uni.request({
                url: 'https://www.jverp.com/auth/login', //仅为示例，并非真实接口地址。
                method:'POST',
                data: {
                    UserName: 'admin',
                    Password: '123456',
                    AccountName:'342',
                    Code:'123',
                    Language:''
                },
                head: {
                'content-type':'application/json'
                },
                success: (res) => {
                    console.log("request success!");
                    console.log(res.data);
                    uni.getStorage({
                        key: 'UserInfo',
                        success: function (res) {
                            console.log('usinfo',res.data);
                        }
                    });
                }
                });
          console.log(res)
            }
        })
        }
      })
    }
  }
})

				// wx.requestSubscribeMessage({
				// 	tmplIds: ['C5LJWaiGWqXTIgXK_D7333xJlqqu3xVHSzskxtJkCaM', 'jkBIEgd9_73LL_oTzl9Hqp7ZeyJjDvg1vzX7aHibfnU'], // 此处可填写多个模板 ID，但低版本微信不兼容只能授权一个
				// 	success(res) {
				// 		console.log('已授权接收订阅消息')
				// 	},
				// 	fail(res) {
				// 		console.log(res)
				// 	}
				// })
			}
		}
	}
</script>

